<template>
    <div id="registerpage">
        <nav-bar :isActive="page_setting.belong"></nav-bar>

        <div class="container mundb-standard-container">
            <div class="row justify-content-sm-center">
                <div class="col-sm-12 col-md-8 col-lg-6">
                    <div class="text-center" style="margin-top:10vh;margin-bottom:20px;">
                        <h1 style="padding:20px;display:inline-block;">CAOJ</h1>
                        <p>CAOJ's yet another Online Judge</p>
                    </div>
                    <div class="card">
                        <div class="card-header">
                            <ul class="nav nav-tabs card-header-tabs nav-justified nav-tabs-material" id="accountTab"
                                role="tablist">
                                <li class="nav-item">
                                    <a class="nav-link" id="login-tab" href="loginpage.html" role="tab">{{$t('DEFAULT.OJ_MESSAGE_LOGIN')}}</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link active" id="register-tab" role="tab">{{$t('DEFAULT.OJ_MESSAGE_REGISTER')}}</a>
                                </li>
                                <div class="nav-tabs-indicator" id="nav-tabs-indicator" style="left: 50%;"></div>
                            </ul>
                        </div>
                        <div class="tab-content" id="accountTabContent">
                            <div class="tab-pane fade show active" id="register" role="tabpanel">
                                <form class="needs-validation" id="register_form" @submit.prevent="registersubmit()">
                                    <div class="card-body">
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.account != '', 'is-focused': current_focus == 'account' && has_focus }">
                                            <label for="account" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_ACCOUNT')}}</label>
                                            <input data-placement="left" data-toggle="tooltip"
                                                   v-bind:data-original-title="$t('WARN.OJ_MESSAGE_ENG_AND_NUM_ONLY')"
                                                   type="text" name="account" class="form-control" id="account"
                                                   required="" v-model="inputs.account" autocomplete="off"
                                                   @focus="is_focus('account');check_account()"
                                                   @blur="is_blur();check_account()" @keyup="check_account">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.account==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'account') || inputs.account.length > 0 ? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.account}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled':inputs.nick != '', 'is-focused': current_focus == 'nick' && has_focus }">
                                            <label for="nick" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_NICK')}}</label>
                                            <input data-placement="left" data-toggle="tooltip"
                                                   v-bind:data-original-title="$t('WARN.OJ_MESSAGE_CHN_ALSO')"
                                                   type="text" name="nick" class="form-control" id="nick"
                                                   autocomplete="off" required="" v-model="inputs.nick"
                                                   @focus="is_focus('nick');check_nick()" @blur="is_blur();check_nick()"
                                                   @keyup="check_nick()">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.nick==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'nick') || inputs.nick.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.nick}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <div style="float: left;width: 70%" class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.email != '', 'is-focused': current_focus == 'email' && has_focus }">
                                            <label for="email" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_EMAIL')}}</label>
                                            <input type="email" name="email" class="form-control" id="email"
                                                   required="" autocomplete="off" v-model="inputs.email"
                                                   @focus="is_focus('email');check_email()"
                                                   @blur="is_blur();check_email()" @keyup="check_email()">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.email==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'email') || inputs.email.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.email}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <template v-if="is_verify_email == true">
                                            <button type="button" disabled="disabled" id="verifying"
                                                    style="margin-bottom: 0px;float: right;width: 30%;margin-top: 26.4px;white-space: pre-line;position: relative;"
                                                    class="btn btn-info" data-placement="left" data-toggle="tooltip"
                                                    v-bind:data-original-title="$t('WARN.OJ_MESSAGE_SENDING_EMAIL')">
                                                <span><i class="fa fa-cog fa-spin fa-3x fa-fw"
                                                         style="font-size: 20px"></i></span> verifying
                                            </button>
                                        </template>
                                        <template v-else-if="remain_verify_time == 0">
                                            <button type="button" id="verify"
                                                    style="margin-bottom: 0px;float: right;width: 25%;margin-top: 28px;white-space: pre-line"
                                                    class="btn btn-info" @click="verify_email()" data-placement="right"
                                                    data-toggle="tooltip"
                                                    v-bind:data-original-title="$t('WARN.OJ_MESSAGE_SEND_EMAIL')">
                                                {{$t('DEFAULT.OJ_MESSAGE_VERIFY_EMAIL')}}
                                            </button>
                                        </template>
                                        <template v-else>
                                            <button type="button" disabled="disabled"
                                                    style="margin-bottom: 0px;float: right;width: 30%;margin-top: 15px;white-space: pre-line"
                                                    class="btn btn-info">
                                                {{$t('WARN.OJ_MESSAGE_RESEND_EMAIL.LEFT')}}{{remain_verify_time}}{{$t('WARN.OJ_MESSAGE_RESEND_EMAIL.RIGHT')}}
                                            </button>
                                        </template>
                                        <div style="clear: both;"></div>
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.vcode != '', 'is-focused': current_focus == 'vcode' && has_focus }">
                                            <label for="vcode" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_VERIFICATION_CODE')}}</label>
                                            <input type="text" name="vcode" class="form-control" value="" id="vcode"
                                                   autocomplete="off" required="" v-model="inputs.vcode"
                                                   @focus="is_focus('vcode')" @blur="is_blur();check_vcode()"
                                                   @keyup="check_vcode()">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.vcode==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'vcode') || inputs.vcode.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.vcode}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <div style="clear: both;"></div>
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.password != '', 'is-focused': current_focus == 'password' && has_focus }">
                                            <label for="password" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_PASSWORD')}}</label>
                                            <input type="password" name="password" class="form-control" id="password"
                                                   autocomplete="off" required="" v-model="inputs.password"
                                                   @focus="is_focus('password');check_password()"
                                                   @blur="is_blur();check_password()" @keyup="check_password()">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.password==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'password') || inputs.password.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.password}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <div class="form-group bmd-form-group "
                                             v-bind:class="{ 'is-filled': inputs.repeat_password != '', 'is-focused': current_focus == 'repeat_password' && has_focus }">
                                            <label for="rptpassword" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_REPEAT_PASSWORD')}}</label>
                                            <input type="password" name="rptpassword" class="form-control"
                                                   id="rptpassword" autocomplete="off" required=""
                                                   v-model="inputs.repeat_password"
                                                   @focus="is_focus('repeat_password');"
                                                   @blur="is_blur();" @keyup="check_repassword()">
                                            <span v-bind:style="{display: 'inline-block', float: 'right', color:error_message.repeat_password==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'repeat_password') || inputs.repeat_password.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.repeat_password}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <!--<input type="hidden" name="csrf" class="form-control" id="csrf"-->
                                        <!--autocomplete="off" required="" v-model="inputs.csrf">-->
                                    </div>
                                    <div class="card-footer text-right">
                                        <a href="javascript:void(0)">
                                            <button name="reset" type="button" class="btn btn-danger"
                                                    @click="reset_input">
                                                {{$t('DEFAULT.OJ_MESSAGE_RESET')}}
                                            </button>
                                        </a>
                                        <button v-if="register_result.is_register == true" disabled="disabled"
                                                name="submit" type="button" class="btn btn-secondary">
                                            <!--{{$t('DEFAULT.OJ_MESSAGE_REGISTER')}}-->
                                            <i class="fa fa-cog fa-spin fa-3x fa-fw" style="font-size: 18px"></i>
                                        </button>
                                        <button v-else name="submit" type="submit" class="btn btn-secondary">
                                            {{$t('DEFAULT.OJ_MESSAGE_REGISTER')}}
                                            <!--<i class="fa fa-cog fa-spin fa-3x fa-fw"></i>-->
                                        </button>
                                        <!--<div @click="kit">5555</div>-->
                                        <!--<a v-bind:href="inputs.account">55555</a>-->
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div class="text-center card-bottom socialite-section"></div>
                    </div>
                </div>
            </div>
        </div>
        <template v-if="model_open == true">
            <div class="modal fade" id="register_error" tabindex="-1" role="dialog"
                 aria-labelledby="register_error_title" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                        <div class="modal-body">
                            <h3 v-if="register_result.has_error == true" style="color: #ff3f4c">
                                {{$t('WARN.OJ_MESSAGE_UNSUCCESSFULLY_REGISTER')}}
                            </h3>
                            <h3 v-else style="color: green">
                                {{$t('WARN.OJ_MESSAGE_SUCCESSFULLY_REGISTER')}}
                            </h3>
                            <br>
                            <p>{{register_result.register_info}}</p>
                        </div>
                        <div class="modal-footer">
                            <button v-if="register_result.has_error == true" type="button" class="btn btn-secondary"
                                    data-dismiss="modal" @click="close_model()">
                                OK
                            </button>
                            <button v-else type="button" class="btn btn-secondary" data-dismiss="modal"
                                    @click="close_register()">
                                OK
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <footers></footers>
    </div>
</template>

<!--</template>-->
<script>
    /* eslint no-useless-escape: "off"*/
    /* eslint vue/valid-template-root: "off"*/
    import NavBar from "@/components/navbar/navbar";
    import fun from '@/assets/js/OJFunction'
    import {hex_md5} from '@/assets/js/md5.js'
    import Footers from "../../components/footer/footer";
    // import

    // Vue.use(VueResource)

    export default {
        components: {Footers, NavBar},
        data() {
            return {
                inputs: {
                    account: '',
                    nick: '',
                    email: '',
                    password: '',
                    vcode: '',
                    repeat_password: '',
                    csrf: ''
                },
                current_focus: '',
                has_focus: false,
                page_setting: {
                    language: "",
                    belong: "account",
                    access_token: "",
                    refresh_token: ""
                },
                error_message: {
                    account: '　',
                    nick: '　',
                    email: '　',
                    password: '　',
                    repeat_password: '　',
                    vcode: '　',
                },
                remain_verify_time: 0,
                is_verify_email: false,
                register_result: {
                    register_info: '',
                    is_register: false,
                    has_error: false
                },
                model_open: false
            }
        },
        methods: {
            registersubmit: function () {
                this.model_open = false;
                this.register_result.register_info = "";
                this.register_result.is_register = true;
                console.log(this.inputs);
                this.inputs.password = hex_md5(this.inputs.password);
                this.inputs.repeat_password = hex_md5(this.inputs.repeat_password);
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/account/register'
                    , this.inputs, {emulateJSON: true}).then(res => {
                    this.register_result.is_register = false;
                    this.model_open = true;
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type != this.GLOBAL.OJ_ERROR_NO_ERROR) this.register_result.has_error = true;
                    if (error_type == this.GLOBAL.OJ_ERROR_NOT_ALLOW_REGISTER) {
                        this.register_result.register_info = this.$t('WARN.OJ_MESSAGE_NOT_ALLOW_REGISTER');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_IP_BE_BANNED) {
                        this.register_result.register_info = this.$t('WARN.OJ_MESSAGE_IP_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_CSRF_INVALID) {
                        this.register_result.register_info = this.$t('WARN.OJ_MESSAGE_CSRF_INVALID');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_WRONG_VCODE) {
                        this.inputs.vcode = "";
                        $("#vcode").focus();
                        this.error_message.vcode = this.$t('WARN.OJ_MESSAGE_WRONG_VCODE');
                        document.getElementById('vcode').setCustomValidity(this.$t('WARN.OJ_MESSAGE_WRONG_VCODE'));
                    } else if (error_type == this.GLOBAL.OJ_ERROR_EXISTENT_ACCOUNT) {
                        $("#account").focus();
                        this.error_message.account = this.$t('WARN.OJ_MESSAGE_EXISTENT_ACCOUNT');
                        document.getElementById('account').setCustomValidity(this.$t('WARN.OJ_MESSAGE_EXISTENT_ACCOUNT'));
                    } else {
                        this.register_result.register_info = this.$t('WARN.OJ_MESSAGE_JUMP_LOGIN');
                    }
                    if (this.register_result.register_info.length > 0) {
                        setTimeout(function () {
                            $('#register_error').modal({
                                backdrop: 'static'
                            })
                        }, 100)
                    }
                    if (this.register_result.has_error == false) {
                        var _this = this;
                        setTimeout(function () {
                            window.location.href = _this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/loginpage.html";
                        }, 4000)
                    }

                }, err => {
                    // error callback
                    console.log(err);
                });
            },
            reset_input: function () {
                this.inputs.account = "";
                this.inputs.password = "";
                this.inputs.vcode = "";
                this.inputs.email = "";
                this.inputs.nick = "";
                this.inputs.repeat_password = "";
            },
            is_focus: function (num) {
                this.current_focus = num;
                this.has_focus = true;
            },
            is_blur: function () {
                this.has_focus = false;
            },
            kit: function () {
                this.register_result.is_register = true;
                // alert(hex_md5(this.inputs.account))
                // window.location.href = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/loginpage.html";
                // this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/test', {
                //     test: this.inputs.account,
                // }, {emulateJSON: true}).then(function (res) {
                //     console.log(res.body);
                //     this.inputs.nick = res.body.test;
                // }, function () {
                //     console.log('请求失败处理');
                // });
                // alert(window.location.href);
                // console.log($("body").remove("div[role^='tooltip']"));

                // $("#verifying").tooltip('show');
                // $("#email").focus();
                // this.error_message.email = "CSRF Code Error or Invalid, please Refresh the Page and Try Again";
                // var data = document.getElementById('email');
                // data.setCustomValidity("CSRF Code Error or Invalid, please Refresh the Page and Try Again");
            },
            check_account: function () {
                var strs = this.inputs.account;
                var data = document.getElementById('account');
                // alert(4);
                if (strs.length <= 3) {
                    this.error_message.account = this.$t('WARN.OJ_MESSAGE_TOO_SHORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_SHORT'));
                } else if (strs.length >= 20) {
                    this.error_message.account = this.$t('WARN.OJ_MESSAGE_TOO_LONG');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_LONG'));
                } else if (strs.replace(/[\W]/g, '') != strs) {
                    this.error_message.account = this.$t('WARN.OJ_MESSAGE_CHN_NOT_SUPPORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_CHN_NOT_SUPPORT'));
                } else if (fun.checkSQL(strs) != "") {
                    var special = fun.checkSQL(strs);
                    this.error_message.account = this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special;
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special);
                } else {
                    this.error_message.account = this.$t('WARN.OJ_MESSAGE_ACCEPTABLE');
                    data.setCustomValidity("");
                }
            },
            check_nick: function () {
                var strs = this.inputs.nick;
                var data = document.getElementById('nick');
                if (strs.length <= 3) {
                    this.error_message.nick = this.$t('WARN.OJ_MESSAGE_TOO_SHORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_SHORT'));
                } else if (strs.length >= 20) {
                    this.error_message.nick = this.$t('WARN.OJ_MESSAGE_TOO_LONG');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_LONG'));
                } else if (fun.checkSQL(strs) != "") {
                    var special = fun.checkSQL(strs);
                    this.error_message.nick = this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special;
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special);
                } else {
                    this.error_message.nick = this.$t('WARN.OJ_MESSAGE_ACCEPTABLE');
                    data.setCustomValidity("");
                }
            },
            check_password: function () {
                var strs = this.inputs.password;
                var data = document.getElementById('password');
                this.check_repassword();
                if (strs.length <= 5) {
                    this.error_message.password = this.$t('WARN.OJ_MESSAGE_TOO_SHORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_SHORT'));
                } else if (strs.length >= 20) {
                    this.error_message.password = this.$t('WARN.OJ_MESSAGE_TOO_LONG');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_LONG'));
                } else if (strs.replace(/[\W]/g, '') != strs) {
                    this.error_message.password = this.$t('WARN.OJ_MESSAGE_CHN_NOT_SUPPORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_CHN_NOT_SUPPORT'));
                } else if (fun.checkSQL(strs) != "") {
                    var special = fun.checkSQL(strs);
                    this.error_message.password = this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special;
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_EXIST_SENSITIVE_WORD') + special);
                } else {
                    this.error_message.password = this.$t('WARN.OJ_MESSAGE_ACCEPTABLE');
                    data.setCustomValidity("");
                }
            },
            check_repassword: function () {
                var strs = this.inputs.repeat_password;
                var data = document.getElementById('rptpassword');
                if (strs.length <= 5) {
                    this.error_message.repeat_password = this.$t('WARN.OJ_MESSAGE_INCONSISTENT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_INCONSISTENT'));
                } else if (strs != this.inputs.password) {
                    this.error_message.repeat_password = this.$t('WARN.OJ_MESSAGE_INCONSISTENT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_INCONSISTENT'));
                } else {
                    this.error_message.repeat_password = this.$t('WARN.OJ_MESSAGE_ACCEPTABLE');
                    data.setCustomValidity("");
                }
            },
            check_email: function () {
                var strs = this.inputs.email;
                var data = document.getElementById('email');
                var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
                if (this.error_message.email == this.$t('WARN.OJ_MESSAGE_CSRF_INVALID')) return;
                if (!reg.test(strs)) {
                    this.error_message.email = this.$t('WARN.OJ_MESSAGE_EMAIL_ILLEGAL_FOEMAT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_EMAIL_ILLEGAL_FOEMAT'));
                } else if (strs.length <= 5) {
                    this.error_message.email = this.$t('WARN.OJ_MESSAGE_TOO_SHORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_SHORT'));
                } else if (strs.length >= 30) {
                    this.error_message.email = this.$t('WARN.OJ_MESSAGE_TOO_LONG');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_LONG'));
                } else {
                    this.error_message.email = this.$t('WARN.OJ_MESSAGE_ACCEPTABLE');
                    data.setCustomValidity("");
                }
            },
            check_vcode: function () {
                var data = document.getElementById('vcode');
                this.error_message.vcode = "";
                data.setCustomValidity("");
            },
            verify_email: function () {
                $('#verify').tooltip('hide');
                var data = document.getElementById('email');
                if (this.inputs.email.length == 0) {
                    $("#email").focus();
                    this.error_message.email = this.$t('WARN.OJ_MESSAGE_TOO_SHORT');
                    data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_TOO_SHORT'));
                    return;
                }
                this.is_verify_email = true;
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/account/verify/email', {
                    email: this.inputs.email,
                    csrf: this.inputs.csrf
                }, {emulateJSON: true}).then(function (res) {
                    this.is_verify_email = false;
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_CSRF_INVALID) {
                        $("#email").focus();
                        this.error_message.email = this.$t('WARN.OJ_MESSAGE_CSRF_INVALID');
                        data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_CSRF_INVALID'));
                    } else if (error_type == this.GLOBAL.OJ_ERROR_EXISTENT_EMAIL) {
                        $("#email").focus();
                        this.error_message.email = this.$t('WARN.OJ_MESSAGE_EXISTENT_EMAIL');
                        data.setCustomValidity(this.$t('WARN.OJ_MESSAGE_EXISTENT_EMAIL'));
                    } else {
                        this.remain_verify_time = 60;
                        this.verify_email_time_updata();
                    }
                }, function () {
                    console.log('请求失败处理');
                });
            },
            verify_email_time_updata: function () {
                console.log(this.remain_verify_time);
                if (this.remain_verify_time > 0) {
                    this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/redis/getExpire', {
                        key: "register_vcode"
                    }, {emulateJSON: true}).then(function (res) {
                        this.remain_verify_time = Number(res.body.OJ_EMAIL_INVALID_TIME);
                        var _this = this;
                        setTimeout(function () {
                            _this.verify_email_time_updata();
                        }, 500)
                        // setTimeout(this.verify_email_time_updata(), 50000);
                    }, function () {
                        console.log('请求失败处理');
                    })
                }
            },
            close_model: function () {
                var _this = this;
                setTimeout(function () {
                    _this.model_open = false;
                    _this.register_result.register_info = "";
                }, 300)
            },
            close_register: function () {
                var _this = this;
                window.location.href = _this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/loginpage.html";
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/registerpage',
                {
                    language: this.page_setting.language
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_HAS_LOGIN) {
                    window.history.go(-1);
                    return;
                }
                fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                this.inputs.csrf = res.body.OJ_REG_CSRF;

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;

                this.remain_verify_time = Number(res.body.OJ_EMAIL_INVALID_TIME);
                this.verify_email_time_updata();
                // if(this.remain_verify_time)

            }, function () {
                console.log('请求失败处理');
            });
        }
    }
</script>
